body {
  background-color: #f6f6f6;
  margin: 0;
  font-family: Noto Sans;
}

form {
  height: 60px;
  background-color: #277bc0;
  justify-content: center;
  align-items: center;
  display: flex;
}

input {
  height: 25px;
  min-width: 500px;
  border: none;
  border-radius: 0%;
  padding: 5px;
  transition: all .2s ease-in-out;
}

input:focus, input:hover {
  outline: none;
  box-shadow: inset 0 10px 20px 2px rgba(0, 0, 0, .1);
}

#search-button {
  height: 35px;
  width: 35px;
  cursor: pointer;
  background-color: #e2e2e2;
  border: none;
}

#search-button:focus, #search-button:hover {
  box-shadow: inset 0 10px 20px 2px rgba(0, 0, 0, .05);
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  padding: 10px;
  display: flex;
}

.photo-card {
  min-width: 320px;
  opacity: .9;
  border: 1px solid rgba(128, 128, 128, .1);
  border-radius: 5px;
  flex-direction: column;
  flex-basis: calc(25% - 20px);
  margin: 3px;
  transition: all .2s ease-in-out;
  display: flex;
  overflow: hidden;
}

.photo-card:hover, .photo-card:focus {
  opacity: 1;
  background-color: #277bc0;
  transform: scale(1.01);
  box-shadow: inset 0 0 20px 10px rgba(0, 0, 0, .1);
}

img {
  height: 30vh;
  width: 100%;
  object-fit: cover;
  display: block;
}

.info {
  color: #f6f6f6;
  cursor: pointer;
  background-color: rgba(39, 123, 192, .5);
  justify-content: space-around;
  display: flex;
}

.info-item {
  text-align: center;
}

